<!-- + -->
<template>
  <div class="t-table-panel">
    <el-table
      class="t-table "
      :class="{ 't-scrollbar': scrollbar }"
      :data="filterTableList"
      :max-height="maxHeigh"
      :stripe="stripe"
      :border="border"
      :highlight-current-row="highlightCurrentRow"
      :row-class-name="rowClassName"
      :default-expand-all="defaultExpandAll"
      :tooltip-effect="tooltipEffect"
      :show-summary="showSummary"
      :sum-text="sumText"
      :summary-method="summaryMethod"
      :span-method="spanMethod"
      :lazy="lazy"
      :load="load"
      :tree-props="treeProps"
      size="small"
      @select="handleSelect"
      @select-all="handleSelectAll"
      @selection-change="handleSelectionChange"
      @row-click="handleRowClick"
      @row-contextmenu="handleRowContextmenu"
      @row-dblclick="handleRowDblclick"
      @header-click="handleHeaderClick"
      @header-contextmenu="handleHeaderContextmenu"
      @header-dragend="handleHeaderDragend"
      v-loading="loading"
      v-scrollbar:table="scrollbar"
      :style="{ height: tableHeight }"
    >
      <slot></slot>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next, jumper, ->, total"
      :total="getPageTotal"
      :page-size="getPageSize"
      :current-page="currentPage"
      :prev-text="prevText"
      :next-text="nextText"
      :disabled="pageDisabled"
      hide-on-single-page
      @current-change="handlePaginationCurrentChange"
    ></el-pagination>
  </div>
</template>

<script>
import _ from 'underscore'

export default {
  name: 'TTablePage',
  props: {
    loading: {
      type: Boolean,
      default: false
    },
    /** perfect-scrollbar */
    scrollbar: {
      type: Boolean,
      default: true
    },
    /** */
    data: Array,
    /**Table  */
    maxHeigh: {
      type: [String, Number]
    },
    /** table */
    stripe: {
      type: Boolean,
      default: true
    },
    /** */
    border: {
      type: Boolean,
      default: true
    },
    /** */
    highlightCurrentRow: {
      type: Boolean,
      default: false
    },
    /**
     *  className  className
     * @param {row, rowIndex}
     */
    rowClassName: {
      type: [Function, String]
    },
    /** Table  */
    defaultExpandAll: {
      type: Boolean,
      default: false
    },
    /**tooltip effect , value: dark/light */
    tooltipEffect: {
      type: String
    },
    /** */
    showSummary: {
      type: Boolean,
      default: false
    },
    /** */
    sumText: {
      type: String,
      default: ''
    },
    /**
     * 
     * @param { columns, data }
     */
    summaryMethod: Function,
    /**
     * 
     * @param { row, column, rowIndex, columnIndex }
     */
    spanMethod: Function,
    /** */
    lazy: Boolean,
    /**
     * lazy  true 
     * @param row
     * @param treeNode
     * @param resolve
     */
    load: Function,
    /**
     * 
     * { hasChildren: 'hasChildren', children: 'children' }
     */
    treeProps: Object,
    /** */
    hasFreeze: {
      type: Boolean,
      default: true
    },
    /** */
    pageSize: Number,
    pageDisabled: {
      type: Boolean,
      default: false
    },
    realTimePage: {
      type: Number,
      default: 1
    },
    /**
     * 
     * 
     * 
     */
    pageTotal: Number,
    prevText: String,
    nextText: String
  },
  data() {
    return {
      currentPage: 1,
      pageStandard: 500
    }
  },
  computed: {
    /** */
    tableHeight() {
      if (this.getPageTotal / this.getPageSize > 1) return 'calc(100% - 42px)'
      else return '100%'
    },
    /** */
    filterTableList() {
      const { pageTotal, pageStandard } = this
      const data = this.data || []
      const flag = !pageSize && data.length >= pageStandard
      const pageSize = this.getPageSize
      if (flag || (pageSize && !pageTotal)) {
        const currentPage = this.currentPage
        const pageIndex = currentPage - 1
        return _.filter(data, (item, index) => {
          return index >= pageIndex * pageSize && index < currentPage * pageSize
        })
      } else if (this.hasFreeze && data.length > 100) return Object.freeze(data)
      else return data
    },
    /** */
    getPageTotal() {
      const data = this.data || []
      return this.pageTotal || data.length
    },
    getPageSize() {
      const { pageTotal, pageSize, pageStandard } = this
      if (pageTotal && !pageSize) return pageStandard
      return pageSize || 10
    }
  },
  watch: {
    loading(value) {
      const flag = value === true || value === false
      if (flag && this.pageTotal === undefined) this.currentPage = 1
    },
    currentPage(value) {
      if (this.$listeners['update:real-time-page'])
        this.$emit('update:real-time-page', value)
    },
    realTimePage(value) {
      this.currentPage = value
    }
  },
  methods: {
    /**
     *  Checkbox 
     * @param selection 
     * @param row
     */
    handleSelect(selection, row) {
      if (this.$listeners['select']) this.$emit('select', selection, row)
    },
    /**
     *  Checkbox 
     * @param selection 
     */
    handleSelectAll(selection) {
      if (this.$listeners['select-all']) this.$emit('select-all', selection)
    },
    /**
     * 
     * @param selection 
     */
    handleSelectionChange(selection) {
      if (this.$listeners['selection-change'])
        this.$emit('selection-change', selection)
    },
    /**
     * 
     * @param row
     * @param column
     * @param event
     */
    handleRowClick(row, column, event) {
      if (this.$listeners['row-click'])
        this.$emit('row-click', row, column, event)
    },
    /**
     * 
     * @param row
     * @param column
     * @param event
     */
    handleRowContextmenu(row, column, event) {
      if (this.$listeners['row-contextmenu'])
        this.$emit('row-contextmenu', row, column, event)
    },
    /**
     * 
     * @param row
     * @param column
     * @param event
     */
    handleRowDblclick(row, column, event) {
      if (this.$listeners['row-dblclick'])
        this.$emit('row-dblclick', row, column, event)
    },
    /**
     * 
     * @param column
     * @param event
     */
    handleHeaderClick(column, event) {
      if (this.$listeners['header-click'])
        this.$emit('header-click', column, event)
    },
    /**
     * 
     * @param column
     * @param event
     */
    handleHeaderContextmenu(column, event) {
      if (this.$listeners['header-contextmenu'])
        this.$emit('header-contextmenu', column, event)
    },
    /**
     * 
     * @param newWidth
     * @param oldWidth
     * @param column
     * @param event
     */
    handleHeaderDragend(newWidth, oldWidth, column, event) {
      if (this.$listeners['header-dragend'])
        this.$emit('header-dragend', newWidth, oldWidth, column, event)
    },
    /**
     * currentPage 
     * @param index 
     */
    handlePaginationCurrentChange(index) {
      this.currentPage = index
      if (this.pageTotal && this.$listeners['current-change'])
        this.$emit('current-change', {
          currentPage: index,
          pageSize: this.getPageSize
        })
    }
  }
}
</script>
